import {
  Avatar,
  IconButton,
  MenuItem,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  Box,
  Popover,
} from "@material-ui/core";
import { __tr } from "../../i18n";
import { MdPerson, MdMoreVert } from "react-icons/md";
import { Link } from "react-router-dom";
import "../../styles/CampaignTable.module.scss";
import { Routes } from "../../routes";
import { usePostedCampaigns } from "../../state/selectors";
import { CssVariables } from "../../css-variables";
import WhatsappSvg from "../../assets/fram.svg";
import { setSelectedCampaign } from "../../state/action-creators";
import { useDispatch } from "react-redux";
import { useAppUser } from "../../state/selectors";
import { useState } from "react";
import axios from "axios";
import { setCampaigns } from "../../state/action-creators";
import { Campaign } from "../../models/index";

export function PostedCampaignTable() {
  const postedCampaigns = usePostedCampaigns();
  const [openOptions, setOpenOptions] = useState<boolean>(false);
  const [menuAnchorEl, setMenuAnchorEl] = useState<HTMLElement>();
  const [selectedItem, setSelectedItem] = useState<any>();

  const dispatch = useDispatch();
  const { user } = useAppUser();
  const handleOptionsModalOpen = (el: HTMLElement, item: any) => {
    setMenuAnchorEl(el);
    setOpenOptions(true);
    setSelectedItem(item);
  };

  //open options modal
  const handleOptionsModalClose = () => {
    setMenuAnchorEl(undefined);
    setOpenOptions(false);
  };
  const token = localStorage.getItem("token");
  const getCampaign = () => {
    if (token) {
      axios
        .get("/api/campaign/" + user._id, { headers: { Authorization: token } })
        .then((res) => {
          const list: Campaign[] = res.data ?? [];
          dispatch(setCampaigns(list));
        });
    }
  };

  // .sort(
  //   (p1, p2) => Date.parse(p2.createdAt || "") - Date.parse(p1.createdAt || "")
  // );

  //const postedCampaigns = [{id:'12asda',Name:"Test cam",CampaignStatus:'Active'}]

  const StatusChange = (input: any, id: any) => {
    const updateCampaginStatus = {
      status: input,
      campaignId: id,
    };

    if (token) {
      console.log(updateCampaginStatus);
      axios

        .post(
          `/api/campaign/changeStatus/`,
          updateCampaginStatus,
          {
            headers: { Authorization: token },
          }
        )
        .then((res: any) => {
          console.log(res.data);
          getCampaign();
        })
        .catch((err: any) => {
          console.log("err", err.response.data);
        });
    }
    setOpenOptions(false);
  };

  return (
    <div className="section">
      <TableContainer>
        <Table style={{ width: "100%" }}>
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell>
                <label className="headline">{__tr("campaigns")}</label>
              </TableCell>
              <TableCell>
                <label className="headline">{__tr("status")}</label>
              </TableCell>
              <TableCell>
                <label className="headline">{__tr("applicants")}</label>
              </TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          {postedCampaigns.length > 0 ? (
            <TableBody>
              {postedCampaigns?.map((p, key) => {
                return (
                  <TableRow key={p?._id}>
                    <TableCell>
                      <Avatar>
                        <MdPerson size={18} />
                      </Avatar>
                    </TableCell>
                    <TableCell>
                      <div className="campaignTitleRow">
                        <Typography variant="body1">{p.Name || ""}</Typography>
                      </div>
                    </TableCell>
                    <TableCell>
                      {__tr(`status_${p.CampaignStatus?.toLowerCase() || ""}`)}
                    </TableCell>
                    <TableCell>
                      <Link
                        to={Routes.viewCampaignApplicants(p?._id || "")}
                        style={{
                          fontWeight: 500,
                          color: CssVariables.colorPrimary,
                        }}
                        onClick={() => {
                          dispatch(setSelectedCampaign(p));
                        }}
                      >
                        View({p.CampaignStatus || 0})
                      </Link>
                    </TableCell>
                    <TableCell>
                      {/* <Link to={Routes.UpdateCampaigns + p?._id}>
                      <IconButton size="small">
                        <MdMoreVert size={24} />
                      </IconButton>
                      </Link> */}
                      {p?.CampaignStatus !== "CLOSED" && (
                        <IconButton
                          onClick={(ev: React.MouseEvent<HTMLElement>) => {
                            handleOptionsModalOpen(ev.currentTarget, p);
                          }}
                        >
                          <MdMoreVert size={24} />
                        </IconButton>
                      )}
                      {selectedItem &&
                        selectedItem?.CampaignStatus !== "CLOSED" && (
                          <Popover
                            open={openOptions}
                            onClose={handleOptionsModalClose}
                            anchorEl={menuAnchorEl}
                            anchorOrigin={{
                              vertical: "bottom",
                              horizontal: "left",
                            }}
                            elevation={2}
                          >
                            <Box minWidth="150px">
                              {/* {selectedItem?.CampaignStatus === "SUBMITTED" ||
                              selectedItem?.CampaignStatus === "DRAFT" ||
                              user?.role === "ADMIN" ? ( */}
                                <MenuItem>
                                   <Link to={Routes.EditCampaign( selectedItem?._id || "")}> 
                                  <span>Edit Campaign</span>
                                   </Link>
                                </MenuItem>
                              {/* ) : null} */}

                              {selectedItem?.CampaignStatus === "PAUSED" ? (
                                <MenuItem
                                  value="RESUME"
                                  onClick={() =>
                                    StatusChange(
                                      "RESUME",
                                      selectedItem?._id || ""
                                    )
                                  }
                                >
                                  Resume Campaign
                                </MenuItem>
                              ) : (
                                <MenuItem
                                  value="PAUSED"
                                  onClick={() =>
                                    StatusChange(
                                      "PAUSED",
                                      selectedItem?._id || ""
                                    )
                                  }
                                >
                                  Pause Campaign
                                </MenuItem>
                              )}

                              {selectedItem?.CampaignStatus === "PRIVATE" ? (
                                <MenuItem
                                  value="PUBLIC"
                                  onClick={() =>
                                    StatusChange(
                                      "PUBLIC",
                                      selectedItem?._id || ""
                                    )
                                  }
                                >
                                  <span>Make Campaign Public</span>
                                </MenuItem>
                              ) : (
                                <MenuItem
                                  value="PRIVATE"
                                  onClick={() =>
                                    StatusChange(
                                      "PRIVATE",
                                      selectedItem?._id || ""
                                    )
                                  }
                                >
                                  <span>Make Campaign Private</span>
                                </MenuItem>
                              )}

                              <MenuItem
                                value="CLOSED"
                                onClick={() =>
                                  StatusChange(
                                    "CLOSED",
                                    selectedItem?._id || ""
                                  )
                                }
                              >
                                <span>Close Campaign </span>
                              </MenuItem>
                              <MenuItem>
                              <Link to={Routes.PostSimilarCampaign(selectedItem?._id || "")}>
                                <span>Post Similar Campaign </span>
                               </Link>
                              </MenuItem>
                            </Box>
                          </Popover>
                        )}
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          ) : (
            <TableBody>
              <TableRow>
                <TableCell colSpan={12}>
                  <div
                    style={{
                      alignItems: "center",
                      justifyContent: "center",
                      textAlign: "center",
                      display: "flex",
                      width: "100%",
                    }}
                  >
                    <img
                      style={{ alignItems: "center", justifyContent: "center" }}
                      src={WhatsappSvg}
                      alt="Logo"
                    />
                  </div>
                </TableCell>
              </TableRow>
            </TableBody>
          )}
        </Table>
      </TableContainer>
    </div>
  );
}
