import {
	Box,
	Checkbox,
	IconButton,
	List,
	ListItem,
	ListItemAvatar,
	ListItemSecondaryAction,
	ListItemText,
	Typography,
	withStyles,
} from '@material-ui/core';
import { useCallback, useEffect, useState } from 'react';
import { useMemo } from 'react';
import { MdAdd, MdRemove } from 'react-icons/md';
import { __tr } from '../../../i18n';
import { DeliverableTypeInsta,Deliverables } from '../../../models/index';
import { DeliverableTypeYoutube } from '../../../models/index';
import { DeliverableType } from '../../../models/index';
import { Platform } from '../../../models/index';


const items = {
	youtube: [
		DeliverableTypeYoutube.DEDICATED_VIDEO,
		DeliverableTypeYoutube.INTEGRATED_VIDEO,
		DeliverableTypeYoutube.SHORT,
	],
	instagram: [
		DeliverableTypeInsta.REEL,
		DeliverableTypeInsta.CONTENT_ONLY,
		DeliverableTypeInsta.STATIC_POST,
		DeliverableTypeInsta.SWIPE_UP_STORY,
		DeliverableTypeInsta.VIDEO_POST,
		DeliverableTypeInsta.IGTV,
	],
};

const StyledButton = withStyles(t => {
	return {
		root: {
			border: `1px solid ${t.palette.divider}`,
		},
	};
})(IconButton);

interface PickerProps {
	platform: Platform;
	deliverables: Deliverables[];
	onChange(values: Deliverables[]): any;
}

export function DeliverablePicker(props: PickerProps) {
	const [counting,setcounting] = useState<Deliverables[]>(); 
	const validDeliverables = useMemo(() => {
		if (props.platform === Platform.INSTAGRAM) {
			return items.instagram;
		} else if (props.platform === Platform.YOUTUBE) {
			return items.youtube;
		}
		return [];
	}, [props.platform]);

	useEffect(()=>{
		setcounting(props.deliverables ?? []);

	},[props])

	const onDeliverableClicked = useCallback(
		(deliverable: DeliverableType) => {
			let exists = props.deliverables.find(d => d.deliverableType === deliverable);
			if (exists) {
				let cpy = props.deliverables.filter(d => d.deliverableType !== deliverable);
				props.onChange(cpy);
			} else {
				let cpy = [...props.deliverables];
				//cpy.push(deliverable);
				props.onChange(cpy);
			}
		},
		[props]
	);

	const incOrDecDeliverable = useCallback(
		(deliverable: DeliverableType, count: number) => {
			console.log('counting',counting );
			// @ts-ignore: Object is possibly 'undefined'.
			const fil = counting.filter(a=> a.deliverableType === deliverable);
			console.log('fil',fil );
			const newcount: typeof counting = [];
			// @ts-ignore: Object is possibly 'undefined'.
			counting.forEach((c,i)=> {
				if(fil.length > 0 && fil[0].deliverableType === c.deliverableType) {
					const del = {deliverableType: deliverable,count: (fil[0].count || 0) + count}
					// @ts-ignore: Object is possibly 'undefined'.
					const idx = counting.findIndex(a=> a.deliverableType === deliverable);
					console.log('here idx',idx);
					if(idx >= 0) {
						newcount.push(del);	
					} 
					//newcount[idx]=del;
					//newcount.push(del);
				} else
				{
					console.log('here else');
					newcount.push(c);
				}
				
			})
			if(fil.length === 0) {
				const del = {deliverableType: deliverable,count:  count > 0 ? count : 1}
				newcount.push(del);
			}
			console.log('newcount',newcount );
			// if(fil.length > 0) {
			// 	console.log(fil[0].count)
			// 	const del = {deliverableType: deliverable,count: (fil[0].count || 0) + count}
			// 	counting[0] = del;
			// } else {
			// 	const del = {deliverableType: deliverable,count: count > 0 ? 1 : 0}
			// 	counting.push(del);
			// }
			// console.log('fil',counting);
			setcounting(newcount);
			props.onChange(newcount)
			// let index = props.deliverables.findIndex(
			// 	d => d.deliverableType === deliverable
			// );
			// if (index >= 0) {
			// 	let cpy = [...props.deliverables] ;
			// 	let newCount =0;
			// 	//cpy &&  cpy[index].count + count;
			// 	if (newCount < 0) {
			// 		newCount = 0;
			// 	}
			// 	//cpy[index].count = newCount;
			// 	props.onChange(cpy);
			// }
		},
		[props,counting]
	);

	return (
		<Box minWidth='53vw'>
			<List dense disablePadding>
				{counting && validDeliverables.map((d,i) => {
					//let existent = props.deliverables.find(e => e.deliverableType === d);
					let existent = counting.find(e => e.deliverableType === d);
					
					//let existent = [{deliverableType:d ,count:0}];
					return (
						<ListItem
							button
							dense
							key={d}
							//onClick={() => onDeliverableClicked(d)}
						>
							{/* <ListItemAvatar>
								 <Checkbox
									color='primary'
									size='small'
									checked={Boolean(existent)}
									//onChange={() => onDeliverableClicked(d)}
								/> 
							</ListItemAvatar> */}
							
								<ListItemAvatar>
									<Box
										display='flex'
										flexDirection='row'
										alignItems='center'
										justifyContent='center'
									>
										<StyledButton
											size='small'
											onClick={() => incOrDecDeliverable(d as DeliverableType, -1)}
										>
											<MdRemove size={14} />
										</StyledButton>
										<Box marginX={1}>
											<Typography variant='body2'>
												{existent?.count || 0}
												
											</Typography>
										</Box>
										<StyledButton
											size='small'
											onClick={() => incOrDecDeliverable(d as DeliverableType, 1)}
										>
											<MdAdd size={14} />
										</StyledButton>
									</Box>
								</ListItemAvatar>
							<ListItemText style={{marginLeft:'12px'}} primary={__tr(d.toLowerCase())} />
							
						</ListItem>
					);
				})}
			</List>
		</Box>
	);
}
